import ImportMetrics from "./components/ImportMetrics"
import AIMetrics from "./components/AIMetrics"
import { importers } from "./config/Importers"
import { AI } from "./config/AIs"

const loadImporterMetrics = importers.map((platform, index) => {
  return <ImportMetrics {...platform} key={platform.platform} />
})
const loadAIMetrics = AI.map((platform, index) => {
  return <AIMetrics {...platform} key={platform.platform} />
})

const App = () => (
  <div className="p-5 mx-auto grid grid-cols-2 gap-y-5">
    <div className="import-logs">{loadImporterMetrics}</div>
    <div className="ai-logs">{loadAIMetrics}</div>
  </div>
)

export default App
