import React, { Component } from "react"
import PacMan from "../../loader/pacMan"
import axios from "axios"
import {
  Bar,
  BarChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts"
import { config } from "../../../config/Settings"
import dayjs from "dayjs"
import { reverseCalcDates } from "../../../helpers/reverseCalcDates"

class AIBarGraph extends Component {
  constructor(props) {
    super(props)
    this.boxName = this.props.boxName
    this.params = this.props.params
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getLogs = this.getLogs.bind(this)
  }

  componentDidMount() {
    this.getLogs()

    this.updateMetric = setInterval(this.getLogs, config.updateInterval)
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getLogs() {
    axios
      .get(`${config.logUrl}/api/logs/ai/count_by_date`, {
        params: this.params,
      })
      .then((response) => {
        console.log(response.data)
        const reverse_result = reverseCalcDates(response.data)
        let logs = reverse_result.map((data) => {
          return {
            x: dayjs(data.date).format("YYYY-MM-DD"),
            y: parseInt(data.count),
          }
        })

        if (this.params.sub_activity === "POST_RESULTS") {
          const new_count = []

          reverse_result.forEach((obj) => {
            if (obj.count > 0) {
              console.log(obj)
              obj.count = obj.count / 2
            }
            new_count.push(obj)
          })

          logs = new_count.map((data) => {
            return {
              x: dayjs(data.date).format("YYYY-MM-DD"),
              y: parseInt(data.count),
            }
          })
        }

        this.setState({ data: logs, loading: false, error: false })
      })
      .catch(() => {
        return this.setState({ error: true, loading: false })
      })
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <ResponsiveContainer width="100%" height={config.graphHeight}>
          <BarChart data={this.state.data}>
            <XAxis dataKey="x" stroke="#222" tick={false} />
            <YAxis dataKey="y" stroke="#222" />
            <Tooltip />
            <Bar dataKey="y" name="Count" fill={this.props.color} />
          </BarChart>
        </ResponsiveContainer>
      )
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default AIBarGraph
