import React from "react"
import PlatformContent from "./PlatformContent"

function ImportMetrics(props) {
  return (
    <div id={props.platform + "-metric"} className="grid gap-y-3">
      <h2 className="capitalize text-left font-bold text-3xl text-gray-900 dark:text-gray-200 subpixel-antialiased">
        {props.platform}
      </h2>
      <PlatformContent
        graphs={props.graphs}
        platform={props.platform}
        color={props.color}
      />
    </div>
  )
}

export default ImportMetrics
