import React, { Component } from "react"
import PacMan from "../../loader/pacMan"
import axios from "axios"
import TextCounter from "../../counters/TextCounter"
import { config } from "../../../config/Settings"

class Average extends Component {
  constructor(props) {
    super(props)
    this.platform = this.props.platform
    this.type = this.props.type
    this.state = {
      data: null,
      loading: true,
      error: false,
    }
    this.getAverage = this.getAverage.bind(this)
  }

  componentDidMount() {
    this.getAverage()

    this.updateMetric = setInterval(this.getAverage, config.updateInterval)
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getAverage() {
    let endpoint = "average",
      url
    if (this.type.includes("_count")) endpoint = "aph"

    url = `${config.logUrl}/api/logs/import/${endpoint}`

    axios
      .get(`${url}`, {
        params: {
          platform: this.platform,
          type: this.type,
        },
      })
      .then((response) => {
        const { avg } = response.data
        this.setState({ data: avg, loading: false, error: false })
      })
      .catch(() => {
        return this.setState({ error: true, loading: false })
      })
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return <TextCounter data={this.state.data} text={this.props.text} />
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default Average
